
import { computed, defineComponent, watch } from "vue";
import { dashboardStore } from "@/store/modules/dashboard";
import { ICallBack, useViewportSize } from "@/hooks/common/useViewportSize";
import router from "@/router";

export default defineComponent({
  name: "LayoutMain",
  setup() {
    const { fn } = useViewportSize({
      callBack({ clientWidth, clientHeight }: ICallBack) {
        dashboardStore.setViewport({
          width: clientWidth,
          height: clientHeight
        });
      }
    });
    watch(() => dashboardStore.menuShrinkState, fn);
    return {
      isFull: computed(() => dashboardStore.isFull),
      isRoute: computed(() =>
        router.currentRoute.value.path.includes("dashboard")
      )
    };
  }
});
