  children: [
    {
      path: "",
      redirect: "/dashboard/big-data"
    },
    {
      path: "big-data",
      meta: {
        name: "大数据看板"
      },
      component: () => import("@/views/dashboard/big-data/big-data.vue")
    },
    {
      path: "realtime-video",
      meta: {
        name: "实时视频"
      },
      component: () =>
        import("@/views/dashboard/realtime-video/realtime-video.vue")
    },
    {
      path: "data-charts",
      meta: {
        name: "综合图表"
      },
      component: () => import("@/views/dashboard/data-charts/data-charts.vue")
    }
  ]
