import { nextTick, onMounted, onUnmounted } from "vue";

export interface IViewPortParam {
  className?: string;
  callBack?: Function;
}

export interface ICallBack {
  clientWidth: number;
  clientHeight: number;
}

export function useViewportSize(param: IViewPortParam) {
  const fn = () => {
    const el = document.querySelector(param.className || ".layout-main");
    if (el) {
      const { clientWidth, clientHeight } = el;
      const ratio = 1080 / 1920;
      let standNum = (clientHeight / 1080) * 0.926 + "vh";
      if (ratio * clientWidth <= clientHeight) {
        standNum = (clientWidth / 1920) * 0.521 + "vw";
      }
      if (param.callBack) {
        param.callBack({ clientWidth, clientHeight });
      }
      document.getElementsByTagName("html")[0].style.fontSize = standNum;
    }
  };
  onMounted(async () => {
    await nextTick();
    fn();
    window.addEventListener("resize", fn);
  });
  onUnmounted(() => {
    window.removeEventListener("resize", fn);
  });
  return {
    fn
  };
}
